body {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}

.navbar {
    position: fixed;
    width: 100%;
    z-index: 2;
    background-color: #415A77;
    color: #010400;
    -webkit-justify-content: normal;
            justify-content: normal;
}

.header {
    font-family: 'Roboto Mono', monospace;
    font-size: 2em;
    margin-left: 22%;
}

@media only screen and (max-width: 1001px) {
    .header {
        font-size: 1.5em;
        margin-left: 15%;
    }
}
.jumbotron {
    background-color: #FAA916;
    margin-bottom: 0;
    padding-top: 100px;
    text-align: center;
}

.score-header {
    font-family: 'Indie Flower', cursive;
}

@media only screen and (max-width: 1001px) {
    .jumbotron {
        padding-top: 130px;
    }
}
.page-footer {
    background-color: #1b263B;
    color: #F7DD72;
}
.pic-div {
    margin: 10px 5px 10px 5px;
    background-color: #F7DD72;
}

.click-img {
    width: 350px;
    height: 250px;
}
